import { useState, useEffect, useCallback, useRef } from 'react';
import { AxiosError } from 'axios';
import api from '../utils/axios';

interface SemanticSearchConfig {
  fields: string[];
  searchBias: number;
  enabled: boolean;
}

const useSemanticSearchConfig = () => {
  const [fields, setFields] = useState<string[]>([]);
  const [searchBias, setSearchBias] = useState<number>(0);
  const enabledRef = useRef(false);
  const setEnabled = (enabled: boolean) => {
    enabledRef.current = enabled;
  };
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [errorTitle, setErrorTitle] = useState<string | null>(null);

  useEffect(() => {
    const controller = new AbortController();

    const fetchSemanticSearchConfig = async () => {
      try {
        setLoading(true);
        const { data } = await api.get<SemanticSearchConfig>(
          'semantic-search',
          {
            signal: controller.signal,
          }
        );
        setFields(data.fields);
        setSearchBias(data.searchBias);
        enabledRef.current = data.enabled;
      } catch (e) {
        if (e instanceof AxiosError) {
          if (e.response?.data.error) {
            setError(e.response?.data.error);
          } else {
            setErrorTitle('Something went wrong');
            setError('please reload the page again.');
          }
        } else {
          setErrorTitle('Something went wrong');
          setError('please reload the page again.');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchSemanticSearchConfig();

    return () => {
      controller.abort();
    };
  }, []);

  const saveSemanticSearchConfig = useCallback(async () => {
    try {
      setLoading(true);
      const { data } = await api.post<SemanticSearchConfig>('semantic-search', {
        fields,
        searchBias,
        enabled: enabledRef.current,
      });
      setFields(data.fields);
      setSearchBias(data.searchBias);
      enabledRef.current = data.enabled;
      return true;
    } catch (e) {
      if (e instanceof AxiosError) {
        setError(e.response?.data.error);
      } else {
        setErrorTitle('Something went wrong');
        setError('please try again.');
      }

      return false;
    } finally {
      setLoading(false);
    }
  }, [fields, searchBias]);

  return {
    fields,
    setFields,
    searchBias,
    setSearchBias,
    enabled: enabledRef.current,
    setEnabled,
    loading,
    error,
    errorTitle,
    saveSemanticSearchConfig,
  };
};

export default useSemanticSearchConfig;
