import ConfigPage from '../pages/Config';
import SyncDataPage from '../pages/SyncData';

export function DefaultView() {
  const settings = window.smartSearchSettings;

  if (
    settings.siteId !== 'NETWORK_ADMIN' &&
    settings.isNetworkActivated === 'true'
  ) {
    return <ConfigPage />;
  }

  return <SyncDataPage />;
}
