import { Grid, Typography, ThemeProvider, Link, Box } from '@mui/material';

import { ReactComponent as DeleteIcon } from './delete-icon.svg';
import { ReactComponent as DocumentIcon } from './document-icon.svg';
import theme, { materialTheme } from '../../theme';
import Button, { ButtonVariant, ButtonSize } from '../../components/Button';
import Card from '../../components/Card';
import { useUnsavedChangeWarning } from '../../hooks/useUnsavedChangeWarning';
import Divider from '@mui/material/Divider';
import { SyncModal } from './SyncModal';
import { SyncStatus } from './types.d';
import useSyncData from '../../hooks/useSyncData';
import { mapSyncStateToAlertVariant } from './helpers';
import Banner from '../../components/Banner/Banner';

const combinedTheme = {
  ...theme,
  ...materialTheme,
};

enum SyncDataColors {
  title = '#1D2939',
  text = '#475467',
  docs = '#0783BE',
}

const typographyTextStyle = {
  fontStyle: 'normal',
  fontWeight: '510',
  fontSize: '16px',
  lineHeight: '24px',
  color: SyncDataColors.title,
  flex: 'none',
  order: '0',
  flexGrow: '0',
};

export default function SyncData() {
  const {
    syncData,
    syncStatus,
    syncMessage,
    deleteStatus,
    deleteMessage,
    progress,
    startPolling,
    cancelSync,
    deleteSyncData,
  } = useSyncData();

  useUnsavedChangeWarning(syncStatus === SyncStatus.Pending);
  const getSyncState = () => ({
    siteName: syncData?.siteName ?? '',
    status: syncStatus,
    deleteStatus: deleteStatus,
    progress,
    syncMessage,
    deleteMessage,
    allSyncedTypes: syncData?.allSyncedTypes ?? [],
    currentSyncedType: syncData?.currentSyncedType ?? null,
  });

  const handleOnRetry = () => {
    startPolling();
  };
  const handleOnClose = () => {
    cancelSync();
  };

  const haveDoneIndex = window.smartSearchSettings.haveDoneIndex || 'true';

  return (
    <ThemeProvider theme={combinedTheme}>
      <Grid container spacing={2}>
        <Grid item xs={8}>
          {haveDoneIndex === 'false' ? (
            <>
              <Banner
                severity="info"
                sx={{ fontSize: '13px' }}
                title="Index search data"
              >
                Welcome! To start things off you must index your data.
              </Banner>
              <br />
            </>
          ) : null}
          <Card title="Index search data" action={null}>
            <Typography
              sx={{
                fontWeight: '400',
                fontSize: '13.5px',
                lineHeight: '20.25px',
              }}
            >
              If you have just installed and activated, or reactivated, WP
              Engine Smart Search Plugin then please rerun this index.
            </Typography>

            {deleteMessage && (
              <Banner
                data-testid="delete-message"
                severity={
                  mapSyncStateToAlertVariant(deleteStatus) === 'danger'
                    ? 'error'
                    : 'success'
                }
              >
                {deleteMessage}
              </Banner>
            )}
            <Grid container justifyContent="space-between">
              <SyncModal
                state={getSyncState()}
                onClick={startPolling}
                onRetry={handleOnRetry}
                onCancel={handleOnClose}
                onClose={handleOnClose}
              />
              <Button
                size={ButtonSize.Medium}
                variant={ButtonVariant.Danger}
                outline
                data-testid="delete-sync-button"
                onClick={deleteSyncData}
                disabled={
                  syncStatus === SyncStatus.Pending ||
                  deleteStatus === SyncStatus.Pending
                }
              >
                <DeleteIcon
                  style={{
                    marginRight: '4px',
                  }}
                />
                Delete search data
              </Button>
            </Grid>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Box
            sx={{
              backgroundColor: 'white',
              padding: '24px',
              borderRadius: '12px',
              border: '1px solid #eaecf0',
              boxShadow: '0px 4px 6px rgba(16, 24, 40, 0.02)',
            }}
          >
            <Typography sx={typographyTextStyle}>About this plugin</Typography>

            <Typography
              sx={{
                ...typographyTextStyle,
                fontWeight: '400',
                fontSize: '14px',
                lineHeight: '21px',
                color: SyncDataColors.text,
                marginTop: '6px',
              }}
            >
              This search solution is tailor-made for WordPress. This add-on
              gives you better speed and greater functionality than your default
              WordPress search.
            </Typography>
            <Divider
              variant="fullWidth"
              sx={{ width: '100%', color: '#EAECF0', margin: '16px 0' }}
            />
            <Typography sx={typographyTextStyle}>Need some help?</Typography>

            <Typography
              sx={{
                ...typographyTextStyle,
                fontWeight: '400',
                fontSize: '14px',
                lineHeight: '21px',
                color: SyncDataColors.text,
                marginTop: '6px',
              }}
            >
              For complete step-by-step instructions, refer to the help
              documentation below:
            </Typography>
            <Typography
              sx={{
                color: SyncDataColors.docs,
                fontWeight: '500',
                fontSize: '13px',
                marginTop: '16px',
              }}
            >
              <Link
                href="https://wpengine.com/support/wp-engine-smart-search/"
                target="_blank"
                rel="noopener noreferrer"
                sx={{ display: 'flex', alignItems: 'center' }}
              >
                <DocumentIcon style={{ marginRight: '8px' }} /> WP Engine Smart
                Search Docs
              </Link>
            </Typography>
          </Box>
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}
