import React from 'react';
import { BrowserRouter, useLocation } from 'react-router-dom';
import { ToastContainer, Flip } from 'react-toastify';

import './App.scss';

import ConfigPage from './pages/Config';
import SettingsPage from './pages/Settings';
import AIPoweredSearch from './pages/AIPoweredSearch';
import { DefaultView } from './utils/router';
import Header from './components/Header';
import HybridSearchConfig from './pages/Config/HybridSearchConfig';

function App() {
  return (
    <>
      <Header />
      <div className="App">
        <BrowserRouter>
          <ToastContainer
            autoClose={2000}
            draggable={false}
            position="top-center"
            transition={Flip}
          />
          <ViewTemplate />
        </BrowserRouter>
      </div>
    </>
  );
}

function ViewTemplate() {
  const query = new URLSearchParams(useLocation().search);
  const view = query.get('view')?.toLocaleLowerCase();

  switch (view) {
    case 'search-config': {
      return <ConfigPage />;
    }

    case 'settings': {
      return <SettingsPage />;
    }

    case 'ai-powered-search': {
      return <AIPoweredSearch />;
    }

    case 'hybrid-settings': {
      return <HybridSearchConfig />;
    }

    default: {
      return <DefaultView />;
    }
  }
}

export default App;
