import { useState } from 'react';

import {
  Grid,
  Typography,
  Box,
  Container,
  Modal,
  ThemeProvider,
  CircularProgress,
} from '@mui/material';
import { toast } from '../../utils/toast';
import theme, { materialTheme } from '../../theme';
import Button, { ButtonVariant, ButtonSize } from '../../components/Button';
import useSemanticSearchConfig from '../../hooks/useSemanticSearchConfig';
import {
  AddSemanticFieldsCard,
  HybridSettingsCard,
  SemanticSettingsCard,
} from './Common';
import useFetchSearchConfig from '../../hooks/useFetchSearchConfig';
import Linkify from 'linkify-react';
import Banner from '../../components/Banner/Banner';

export function SemanticHybridConfig() {
  const {
    error: semanticConfigError,
    fields,
    setEnabled,
    setFields,
    searchBias,
    setSearchBias,
    saveSemanticSearchConfig,
  } = useSemanticSearchConfig();
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const { loading, error: searchConfigError, config } = useFetchSearchConfig();

  const [textFieldValue, setTextFieldValue] = useState('');
  const chooseField = (field: string) => {
    setFields([field]);
  };
  const removeField = () => {
    setFields([]);
  };
  const handleTextFieldChange = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    setTextFieldValue(event.target.value);
  };
  const error = semanticConfigError || searchConfigError;
  const saveAndConfigure = async () => {
    setEnabled(true);
    const result = await saveSemanticSearchConfig();
    if (result) {
      toast(
        'Saved and configured! Please reindex for any field changes that have been made.',
        'success',
        {
          autoClose: false,
        }
      );
    }
  };

  if (loading) {
    return <CircularProgress />;
  }

  if (error) {
    return (
      <ThemeProvider theme={materialTheme}>
        <Container
          data-testid="search-config-container"
          maxWidth="xl"
          disableGutters
        >
          <Grid container spacing={2} mb={2}>
            <Grid item xs={8}>
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                Hybrid Settings
              </Typography>
            </Grid>
          </Grid>
          <Banner severity="error" data-testid="search-config-alert">
            <Linkify>{error}</Linkify>
          </Banner>
        </Container>
      </ThemeProvider>
    );
  }

  if (!config) {
    return <div>no config</div>;
  }
  return (
    <ThemeProvider theme={materialTheme}>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box>
          <AddSemanticFieldsCard
            config={config}
            fields={fields}
            chooseField={chooseField}
            textValue={textFieldValue}
            handleTextValueChange={handleTextFieldChange}
            handleClose={handleClose}
          />
        </Box>
      </Modal>
      <Container
        data-testid="semantic-hybrid-config-container"
        maxWidth="xl"
        disableGutters
      >
        <HybridSettingsCard
          action={
            <ThemeProvider theme={theme}>
              <Button
                size={ButtonSize.Medium}
                variant={ButtonVariant.Primary}
                onClick={() => saveAndConfigure()}
                data-testid="save-button"
              >
                Save Configuration
              </Button>
            </ThemeProvider>
          }
          searchBias={searchBias}
          setSearchBias={setSearchBias}
        />
        <br />
        <SemanticSettingsCard
          fields={fields}
          removeField={removeField}
          handleOpen={handleOpen}
        />
      </Container>
    </ThemeProvider>
  );
}

export default SemanticHybridConfig;
