import { ReactComponent as ReloadIcon } from './reload-icon.svg';
import { ReactComponent as DotIcon } from './dot-icon.svg';
import theme from '../../theme';
import Icon from '../../components/Icon';
import { SyncStatus } from './types.d';
import { Box, CircularProgress, Grid, Typography } from '@mui/material';
import { ProgressStatusColor } from '../../components/ProgressBar/StyledProgressBar';

export function TriggerText({ status }: Pick<SyncState, 'status'>) {
  return (
    <>
      {status === SyncStatus.Error ? (
        <Icon name="controls-play" mr={1} />
      ) : (
        <ReloadIcon style={{ marginRight: '8px' }} />
      )}
      {status === SyncStatus.Error ? 'Resume' : 'Index Now'}
    </>
  );
}

export function SyncSuccessMessage({ status }: Pick<SyncState, 'status'>) {
  let message = null;

  if (status === SyncStatus.Completed) {
    message = 'Sync successful!';
  } else if (status === SyncStatus.Error) {
    message = 'Index failed';
  }

  return message && <Typography>{message}</Typography>;
}

export function SyncErrorMessage({
  status,
  message,
}: {
  status: SyncStatus;
  message?: string;
}) {
  if (status === SyncStatus.Error && message) {
    return <Alert message={message} variant={status} />;
  }
  return null;
}

export function SyncPostTypes({
  allSyncedTypes,
  currentSyncedType,
  status,
}: {
  allSyncedTypes: string[];
  currentSyncedType: string | null;
  status: SyncStatus;
}) {
  const currentSyncedTypeIndex = currentSyncedType
    ? allSyncedTypes.indexOf(currentSyncedType)
    : -1;
  const isCompleted = status === SyncStatus.Completed;
  const isError = status === SyncStatus.Error;
  return (
    <Grid
      container
      spacing={1}
      sx={{ m: '16px 0 0 0' }}
      height="24px"
      minWidth="200px"
    >
      {allSyncedTypes.map((syncType, i) => (
        <Grid key={i}>
          <Box sx={{ mr: 2, textAlign: 'left' }}>
            {currentSyncedType === syncType ? (
              !isError ? (
                <Box
                  display="inline-flex"
                  sx={{ width: '20px', height: '20px' }}
                >
                  <CircularProgress size={8} />
                </Box>
              ) : (
                <Icon
                  name="warning"
                  style={{
                    color: theme.colors.progressError,
                    fontSize: '16px',
                    width: '16px',
                    height: '16px',
                    verticalAlign: 'text-top',
                  }}
                />
              )
            ) : currentSyncedTypeIndex > i || isCompleted ? (
              <Icon
                name="yes"
                style={{ color: theme.colors.progressSuccess }}
              />
            ) : (
              <DotIcon style={{ width: '20px' }} />
            )}
            {syncType}
          </Box>
        </Grid>
      ))}
    </Grid>
  );
}

export function Alert({
  message,
  variant,
}: {
  message: string;
  variant: SyncStatus;
}) {
  return (
    <Box
      sx={{
        background: '#F8F8F8',
        border: '1px solid #eee',
        borderRadius: '4px',
        padding: '9px 16px',
        mt: 3,
      }}
      data-testid={`${variant}-message`}
    >
      {message}
    </Box>
  );
}

export const progressBarStatusMap: Record<SyncStatus, ProgressStatusColor> = {
  [SyncStatus.Init]: ProgressStatusColor.Primary,
  [SyncStatus.Pending]: ProgressStatusColor.Primary,
  [SyncStatus.Completed]: ProgressStatusColor.Complete,
  [SyncStatus.Error]: ProgressStatusColor.Danger,
};

export type SyncState = {
  siteName: string;
  status: SyncStatus;
  deleteStatus: SyncStatus;
  progress: number;
  syncMessage: string;
  allSyncedTypes: string[];
  currentSyncedType: string | null;
};
