import * as React from 'react';
import { Typography, ThemeProvider, Stack, Box } from '@mui/material';

import { BasicModal } from '../../components/Modal';
import theme from '../../theme';

import ProgressBar from '../../components/ProgressBar';
import Button, { ButtonVariant } from '../../components/Button';

import { SyncStatus } from './types.d';
import {
  SyncPostTypes,
  SyncState,
  SyncSuccessMessage,
  TriggerText,
  progressBarStatusMap,
  SyncErrorMessage,
} from './SyncModalHelpers';

interface SyncModalProps {
  state: SyncState;
  onClick: () => void;
  onRetry?: () => void;
  onCancel?: () => void;
  onClose?: () => void;
}

export function SyncModal({
  state,
  onClick,
  onRetry,
  onCancel,
  onClose,
}: SyncModalProps) {
  const {
    status,
    deleteStatus,
    progress,
    syncMessage,
    allSyncedTypes,
    currentSyncedType,
  } = state;
  const [open, setOpen] = React.useState(false);
  const handleToggleModal = () => {
    setOpen((current) => !current);
  };
  const handleRetry = () => {
    if (onRetry) onRetry();
  };
  const handleCancel = () => {
    if (onCancel) onCancel();
    handleToggleModal();
  };
  const handleClose = () => {
    if (onClose) onClose();
    handleToggleModal();
  };
  return (
    <BasicModal
      open={open}
      handleToggleModal={handleToggleModal}
      onClick={onClick}
      triggerText={<TriggerText status={status} />}
      triggerProps={{
        'data-testid': 'sync-button',
        disabled:
          status === SyncStatus.Pending || deleteStatus === SyncStatus.Pending,
      }}
      disableBackdropClick={status === SyncStatus.Pending}
      content={
        <Stack>
          <Typography
            id="sync-modal-title"
            variant="h6"
            component="h2"
            fontWeight="600"
            sx={{ mb: 4 }}
          >
            Index {state.siteName}
            <SyncSuccessMessage status={status} />
          </Typography>
          <Typography
            sx={{
              fontWeight: 400,
              fontSize: '28px',
              lineHeight: '70%',
              mb: 2,
            }}
          >
            {progress}%
          </Typography>
          <ThemeProvider theme={theme}>
            <ProgressBar
              data-testid="sync-percentage"
              value={progress}
              status={progressBarStatusMap[status]}
            />
          </ThemeProvider>
          <SyncPostTypes
            allSyncedTypes={allSyncedTypes}
            currentSyncedType={currentSyncedType}
            status={status}
          />
          <SyncErrorMessage message={syncMessage} status={status} />
          <Box display="flex" sx={{ mt: 3 }}>
            {status === SyncStatus.Init || status === SyncStatus.Completed ? (
              <Box>
                <Button
                  id="sync-modal-close"
                  variant={ButtonVariant.Primary}
                  onMouseDown={handleClose}
                >
                  Close
                </Button>
              </Box>
            ) : (
              <Box>
                <Button
                  id="sync-modal-cancel"
                  variant={ButtonVariant.Danger}
                  onMouseDown={handleCancel}
                >
                  Cancel
                </Button>
              </Box>
            )}
            {status === SyncStatus.Error && (
              <Box sx={{ ml: 1 }}>
                <Button
                  id="sync-modal-retry"
                  variant={ButtonVariant.Primary}
                  outline
                  onMouseDown={handleRetry}
                >
                  Retry
                </Button>
              </Box>
            )}
          </Box>
        </Stack>
      }
    />
  );
}
