import { useState } from 'react';
import theme, { materialTheme } from '../../theme';
import {
  Alert,
  Box,
  Chip,
  CircularProgress,
  Container,
  Grid,
  List,
  ListItemButton,
  ListItemText,
  Modal,
  Slider,
  ThemeProvider,
  Typography,
  styled,
} from '@mui/material';
import Linkify from 'linkify-react';

import useSearchConfig from '../../hooks/useFetchSearchConfig';
import useSemanticSearchConfig from '../../hooks/useSemanticSearchConfig';
import { toast } from '../../utils/toast';
import Card from '../../components/Card';
import Button, { ButtonSize, ButtonVariant } from '../../components/Button';
import StyledSwitch from '../../components/Switch/StyledSwitch';
import Divider from '@mui/material/Divider';

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 500,
  maxHeight: '80%',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  overflow: 'auto',
  p: 4,
};

export default function AIPoweredSearch() {
  const {
    config,
    loading: searchConfigLoading,
    error: searchConfigError,
  } = useSearchConfig();
  const {
    fields,
    setFields,
    saveSemanticSearchConfig,
    enabled,
    setEnabled,
    searchBias,
    setSearchBias,
    error: semanticError,
  } = useSemanticSearchConfig();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const chooseField = (field: string) => {
    setFields([field]);
    setOpen(false);
  };

  const removeField = (field: string) => {
    setFields([]);
  };

  const saveAndConfigure = async () => {
    const result = await saveSemanticSearchConfig();
    if (result) {
      toast(
        'Saved and configured! Please reindex for any field changes that have been made.',
        'success',
        {
          autoClose: false,
        }
      );
    }
  };

  const Error = ({ error }: { error: string }) => {
    return (
      <Grid mb={2}>
        <Alert severity="error" data-testid="ai-powered-search-error">
          <Linkify>{error}</Linkify>
        </Alert>
      </Grid>
    );
  };

  if (searchConfigLoading) {
    return <CircularProgress />;
  }

  const BorderedList = styled(List)(({ theme }) => ({
    borderLeftWidth: 5,
    borderLeftStyle: 'solid',
    borderColor: theme.wpColors.tiffany,
    boxShadow: theme.shadows[3],
    marginTop: theme.spacing(4),
  }));

  const marks = [
    {
      value: 0,
      label: 'Full text search only',
    },
    {
      value: 5,
      label: 'Mix of both',
    },
    {
      value: 10,
      label: 'Semantic Search only',
    },
  ];

  const disableOnFlag = (isError: boolean) => {
    return {
      opacity: isError ? 0.5 : 1,
      pointerEvents: isError ? 'none' : 'initial',
    };
  };

  const getSearchType = (): string => {
    const mark = marks.find((m) => m.value === searchBias);
    return mark ? mark.label : 'Mix of both';
  };

  return (
    <ThemeProvider theme={materialTheme}>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            Choose a field
          </Typography>
          <Container maxWidth="xl" disableGutters={true}>
            {!config && (
              <Error error="No config found, please try reloading the page." />
            )}
            {config &&
              Object.keys(config.models).map((modelName) => {
                return (
                  <BorderedList
                    key={`bordered-list-${modelName}`}
                    sx={{
                      width: '100%',
                      maxWidth: 400,
                      paddingLeft: 2,
                      bgcolor: 'background.paper',
                    }}
                  >
                    <Typography variant="h6">{modelName}</Typography>
                    {Object.entries(config.models[modelName])
                      .filter(([key, value]) => !value.has_sub_fields)
                      .map(([field]) => {
                        return (
                          <ListItemButton
                            key={`list-item-${modelName}-${field}`}
                            onClick={() => chooseField(field)}
                          >
                            <ListItemText>{field}</ListItemText>
                          </ListItemButton>
                        );
                      })}
                  </BorderedList>
                );
              })}
          </Container>
        </Box>
      </Modal>

      {/* Errors */}
      {(searchConfigError || semanticError) && (
        <Container
          data-testid="ai-powered-search-error-container"
          maxWidth="xl"
        >
          {searchConfigError && <Error error={searchConfigError} />}
          {semanticError && <Error error={semanticError} />}
        </Container>
      )}
      <Container
        sx={disableOnFlag(searchConfigError !== null || semanticError !== null)}
        data-testid="ai-powered-search-container"
        maxWidth="xl"
      >
        <Card
          title="AI-Powered Hybrid Search"
          action={
            <ThemeProvider theme={theme}>
              <Button
                size={ButtonSize.Medium}
                variant={ButtonVariant.Primary}
                onClick={() => saveAndConfigure()}
                data-testid="save-button"
              >
                Save Configuration
              </Button>
            </ThemeProvider>
          }
        >
          <Grid container>
            <Grid item xs={8}>
              <Typography variant="subtitle1" gutterBottom>
                This configuration incorporates semantics into the search along
                with standard full text search.
              </Typography>
            </Grid>
            <Grid
              container
              item
              xs={4}
              justifyContent="right"
              sx={{ alignItems: 'center' }}
            >
              <Grid item data-testid="enable-button">
                <StyledSwitch
                  checked={enabled}
                  onChange={(_event, newValue) => {
                    setEnabled(newValue);
                  }}
                  inputProps={{
                    role: 'hybrid-switch',
                  }}
                />
              </Grid>
              <Grid item>
                <Typography variant="subtitle1" gutterBottom>
                  Enabled
                </Typography>
              </Grid>
            </Grid>
          </Grid>

          <Divider
            variant="fullWidth"
            sx={{ width: '100%', color: '#EAECF0' }}
          />

          {/* Config slider */}
          <Grid sx={disableOnFlag(!enabled)} container spacing={2} mt={1}>
            <Grid item xs={5}>
              <Typography variant="h6">
                Search Bias ({getSearchType()})
              </Typography>
            </Grid>
            <Grid item xs={6}>
              <Slider
                aria-label="config"
                min={0}
                max={10}
                marks={marks}
                value={searchBias}
                onChange={(_event, newValue) =>
                  setSearchBias(newValue as number)
                }
              />
            </Grid>
          </Grid>

          <Divider
            variant="fullWidth"
            sx={{ width: '100%', color: '#EAECF0' }}
          />

          {/*  Main content */}
          <Grid
            sx={disableOnFlag(!enabled)}
            container
            direction="column"
            mt={1}
          >
            <Grid item xs={12}>
              <Typography variant="h6">Fields ({fields.length})</Typography>
            </Grid>

            <Grid item container>
              <Grid item xs={8} sx={{ alignContent: 'center' }}>
                <Typography variant="subtitle1">
                  Select a field from your database to leverage for Semantic
                  Search.{' '}
                </Typography>
              </Grid>

              <Grid item xs={4} textAlign="right" sx={{ alignItems: 'center' }}>
                <ThemeProvider theme={theme}>
                  <Button
                    variant={ButtonVariant.Primary}
                    onClick={handleOpen}
                    data-testid="modal-button"
                  >
                    {fields.length > 0 ? 'Edit Field' : 'Add Field'}
                  </Button>
                </ThemeProvider>
              </Grid>
            </Grid>

            <Grid container mt={1}>
              {fields.map((field) => (
                <Chip
                  key={`field-${field}`}
                  color="default"
                  label={field}
                  size="medium"
                  variant="outlined"
                  onDelete={() => removeField(field)}
                />
              ))}
            </Grid>
          </Grid>
        </Card>
      </Container>
    </ThemeProvider>
  );
}
