import * as React from 'react';
import styled from '@emotion/styled';
import Alert from '@mui/material/Alert';
import { AlertColor } from '@mui/material/Alert';
import { SxProps } from '@mui/material';
import { ReactComponent as InfoIcon } from './info-icon.svg';
import { ReactComponent as WarningIcon } from './warning-icon.svg';
import { ReactComponent as ErrorIcon } from './error-icon.svg';
import { ReactComponent as SuccessIcon } from './success-icon.svg';

const StyledAlert = styled(Alert)`
  &.MuiAlert-root {
    box-shadow: none;
    border-radius: 8px;
    height: 48px;
    border: 1px solid
      ${(props) => {
        switch (props.severity) {
          case 'info':
            return '#9dbaee';
          case 'error':
            return '#F1B6B3';
          case 'warning':
            return '#F4DBB4';
          case 'success':
            return '#B6DEB9';
          default:
            return null;
        }
      }};
    background: ${(props) => {
      switch (props.severity) {
        case 'info':
          return '#E7EFF9';
        case 'error':
          return '#F7EEEB';
        case 'warning':
          return '#FDF8EB';
        case 'success':
          return '#EDF7EF';
        default:
          return null;
      }
    }};
    align-items: center;
    padding: 0;
    .MuiAlert-icon {
      padding-left: 5px;
      margin-top: 7px;
    }
  }
`;

const getIcon = (severity: AlertColor) => {
  switch (severity) {
    case 'info':
      return <InfoIcon />;
    case 'error':
      return <ErrorIcon />;
    case 'warning':
      return <WarningIcon />;
    case 'success':
      return <SuccessIcon />;
    default:
      return null;
  }
};

interface AlertProps {
  severity: AlertColor;
  action?: React.ReactNode;
  title?: string | null;
  dataTestid?: string;
  sx?: SxProps;
}

function Banner({
  severity,
  action,
  title,
  dataTestid,
  sx,
  children,
}: React.PropsWithChildren<AlertProps>) {
  return (
    <StyledAlert
      data-testid={dataTestid}
      sx={sx}
      severity={severity}
      action={action}
      icon={getIcon(severity)}
    >
      {title && <b>{title} - </b>}
      {children}
    </StyledAlert>
  );
}

export default Banner;
