import {
  Grid,
  Typography,
  FormLabel,
  Slider,
  Container,
  Tooltip,
  IconButton,
} from '@mui/material';
import { useState } from 'react';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';

import { Config, Fields } from './types';

import StyledSwitch from '../../components/Switch/StyledSwitch';

const fieldTooltipText =
  "Field Name is the unique identifier for the WordPress data you've indexed for search. When you search by field name, you are looking for data associated with these specific labels.";
const searchableTooltipText =
  'The searchable checkboxes allow for the inclusion/exclusion of a field from the search. Check the items to be searchable and uncheck the items to make them not searchable.';
const weightTooltipText =
  'The field-level weight sliders allow for some fields to be considered more relevant than others when determining the order of search results. Use the sliders to assign different weights to the searchable data fields. A field with a higher weight will be given a higher priority in the search results.';

interface FieldProps {
  modelName: string;
  name: string;
  config: Config;
  onChange: (fieldName: string, updatedConfig: FieldUpdate) => void;
}

interface FieldUpdate {
  searchable?: boolean;
  weight?: number;
}

interface Props {
  name: string;
  fields: Fields;
  onChange: (fieldName: string, updatedConfig: FieldUpdate) => void;
}

export default function ConfigBlock(props: Props) {
  const { name, fields, onChange } = props;

  const baseFields: Array<string> = [];
  const additionalFields: Array<string> = [];

  Object.keys(fields).forEach((field) => {
    if (field.includes('.')) {
      additionalFields.push(field);
    } else {
      baseFields.push(field);
    }
  });

  return (
    <Container disableGutters>
      <Grid
        container
        key={`${name}-base-header`}
        sx={{
          backgroundColor: '#F9FAFB',
          borderTop: '1px solid #EAECF0',
          height: '40px',
        }}
      >
        <Grid item key={name} xs={12}>
          <Typography variant="h5">{name}</Typography>
        </Grid>

        <Grid item xs={4}>
          <Typography
            variant="body2"
            sx={{
              paddingLeft: '24px',
            }}
          >
            Field Name
            <Tooltip title={fieldTooltipText}>
              <IconButton>
                <InfoOutlinedIcon
                  sx={{ height: '16px', width: '16px', color: '#667085' }}
                />
              </IconButton>
            </Tooltip>
          </Typography>
        </Grid>

        <Grid item xs={4}>
          <Typography variant="body2">
            Searchable
            <Tooltip title={searchableTooltipText}>
              <IconButton>
                <InfoOutlinedIcon
                  sx={{ height: '16px', width: '16px', color: '#667085' }}
                />
              </IconButton>
            </Tooltip>
          </Typography>
        </Grid>

        <Grid item xs={4}>
          <Typography variant="body2">
            Weight
            <Tooltip title={weightTooltipText}>
              <IconButton>
                <InfoOutlinedIcon
                  sx={{ height: '16px', width: '16px', color: '#667085' }}
                />
              </IconButton>
            </Tooltip>
          </Typography>
        </Grid>
      </Grid>

      {baseFields.map((field) => (
        <Grid
          key={`base-grid-config-${field}`}
          sx={{
            borderTop: '1px solid #EAECF0',
            paddingLeft: '24px',
            height: '64px',
            alignContent: 'center',
          }}
        >
          <Field
            key={`base-field-config-${field}`}
            name={field}
            config={fields[field]}
            modelName={name}
            onChange={onChange}
          />
        </Grid>
      ))}

      {additionalFields.length > 0 && (
        <Grid
          container
          key={`${name}-aditional-header`}
          sx={{
            backgroundColor: '#F9FAFB',
            borderTop: '1px solid #EAECF0',
            height: '40px',
            alignContent: 'center',
          }}
        >
          <Grid item xs={4}>
            <Typography
              variant="body2"
              sx={{
                paddingLeft: '24px',
              }}
            >
              Additional Fields
              <Tooltip title={fieldTooltipText}>
                <IconButton>
                  <InfoOutlinedIcon
                    sx={{ height: '16px', width: '16px', color: '#667085' }}
                  />
                </IconButton>
              </Tooltip>
            </Typography>
          </Grid>

          <Grid item xs={3.8}>
            <Typography variant="body2">
              Searchable
              <Tooltip title={searchableTooltipText}>
                <IconButton>
                  <InfoOutlinedIcon
                    sx={{ height: '16px', width: '16px', color: '#667085' }}
                  />
                </IconButton>
              </Tooltip>
            </Typography>
          </Grid>

          <Grid item xs={4}>
            <Typography variant="body2">
              Weight
              <Tooltip title={weightTooltipText}>
                <IconButton>
                  <InfoOutlinedIcon
                    sx={{ height: '16px', width: '16px', color: '#667085' }}
                  />
                </IconButton>
              </Tooltip>
            </Typography>
          </Grid>
        </Grid>
      )}

      {additionalFields.map((field) => (
        <Grid
          key={`aditional-grid-config-${field}`}
          sx={{
            borderTop: '1px solid #EAECF0',
            paddingLeft: '24px',
            height: '64px',
            alignContent: 'center',
          }}
        >
          <Field
            key={`field-config-${field}`}
            name={field}
            config={fields[field]}
            modelName={name}
            onChange={onChange}
          />
        </Grid>
      ))}
    </Container>
  );
}

function Field(props: FieldProps) {
  const { name, config, onChange } = props;
  const [weight, setWeight] = useState(config.weight);

  return (
    <Grid
      container
      key={`${name}-item`}
      direction="row"
      justifyContent="center"
      alignItems="center"
    >
      <Grid item xs={4}>
        <FormLabel>{name}</FormLabel>
      </Grid>

      <Grid item xs={4}>
        <StyledSwitch
          checked={config.searchable}
          inputProps={{ role: 'config-switch' }}
          onChange={() => onChange(name, { searchable: !config.searchable })}
        />
      </Grid>

      <Grid item xs={4}>
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="center"
        >
          <FormLabel
            disabled={!config.searchable}
            sx={{
              backgroundColor: '#ffffff',
              border: '1px solid #C6CBD3',
              boxShadow: '0px 1px 2px rgba(16, 24, 40, 0.06)',
              borderRadius: '6px',
              display: 'flex',
              justifyContent: 'center',
              width: '48px',
              height: '32px',
              right: '16px',
              placeItems: 'center',
            }}
          >
            {weight}
          </FormLabel>

          <Slider
            disabled={!config.searchable}
            sx={{
              width: '70%',
              height: '4px',
              '& .MuiSlider-thumb': {
                width: 18,
                height: 18,
              },
            }}
            defaultValue={config.weight}
            max={200}
            min={1}
            onChangeCommitted={(e, newValue) => {
              onChange(name, { weight: newValue as number });
            }}
            onChange={(e, value) => {
              setWeight(value as number);
            }}
          />
        </Grid>
      </Grid>
    </Grid>
  );
}
