import React, { useState, useEffect, useRef } from 'react';
import {
  Box,
  Container,
  IconButton,
  Modal,
  ThemeProvider,
  Typography,
} from '@mui/material';
import { ThemeProvider as EmotionThemeProvider } from '@emotion/react';

import theme, { materialTheme } from '../../theme';
import Form from '../../components/Form';
import { toast } from '../../utils/toast';
import axios from '../../utils/axios';
import Card from '../../components/Card';
import Button, { ButtonSize, ButtonVariant } from '../../components/Button';
import Banner from '../../components/Banner/Banner';
import CloseIcon from '@mui/icons-material/Close';
import { ReactComponent as WarningIcon } from './warning-icon.svg';
import { useUnsavedChangeWarning } from '../../hooks/useUnsavedChangeWarning';

const style = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  alignItems: 'flex-start',
  padding: '0px',
  width: '600px',
  height: '355px',
  background: '#FFFFFF',
  border: '1px solid rgba(31, 41, 55, 0.08)',
  boxShadow: '0px 25px 50px -12px rgba(0, 0, 0, 0.25)',
  borderRadius: '12px',
  flex: 'none',
  order: '3',
  flexGrow: '0',
  zIndex: '3',
  flexDirection: 'column',
  '.MuiCardHeader-root': {
    height: '64px',
    '.MuiTypography-root': {
      marginTop: '5px',
    },
  },
  '.MuiCardContent-root': {
    padding: '24px',
  },
  '.MuiCardHeader-action': {
    '.MuiButtonBase-root': {
      padding: '0px',
    },
  },
};

export interface SearchSettingsConfig {
  url: string;
  access_token: string;
}

function Settings() {
  const [url, setUrl] = useState('');
  const [token, setToken] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const submitButton = useRef<HTMLButtonElement>(null);

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setOpen(false);
  };
  const handleConfirmClose = async () => {
    let result;
    setOpen(false);
    result = await saveSettings();
    if (result) {
      setCurrentUrl(url);
      setCurrentToken(token);
      setConfigSet(true);
      setConfigChanged(false);
    }
  };

  useEffect(() => {
    const fetchSettingsData = async () => {
      setErrorMessage('');

      try {
        // eslint-disable-next-line
        const { data: settings } =
          await axios.get<SearchSettingsConfig>('settings');
        if (settings !== null) {
          setUrl(settings.url);
          setToken(settings.access_token);
        }
      } catch (e) {
        setErrorMessage(
          'Unexpected sync error occurred. Problem reading server configuration!'
        );
      }
    };

    fetchSettingsData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleUrlChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setUrl(e.target.value);
  };

  const handleTokenChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setToken(e.target.value);
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setErrorMessage('');

    if (submitButton.current) {
      submitButton.current.blur();
    }

    handleOpen();
  };

  const saveSettings = async () => {
    try {
      await axios.post('settings', { url: url, access_token: token });
      toast('Settings saved!', 'success');
    } catch (e) {
      setErrorMessage(
        "Unexpected error occurred! Couldn't save server configuration"
      );
      return false;
    }
    return true;
  };

  const [configChanged, setConfigChanged] = useState(false);
  const initialUrlRef = useRef(url);
  const [currentUrl, setCurrentUrl] = useState(initialUrlRef.current);
  const initialTokenRef = useRef(token);
  const [currentToken, setCurrentToken] = useState(initialTokenRef.current);
  const [configSet, setConfigSet] = useState(false);

  useEffect(() => {
    if (url && token && configSet === false) {
      setCurrentUrl(url);
      setCurrentToken(token);
      setConfigSet(true);
    }
  }, [configSet, token, url]);

  useEffect(() => {
    if (currentUrl !== url || currentToken !== token) {
      setConfigChanged(true);
    } else {
      setConfigChanged(false);
    }
  }, [currentToken, currentUrl, setConfigChanged, token, url, configChanged]);

  useUnsavedChangeWarning(configChanged);

  return (
    <ThemeProvider theme={materialTheme}>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box>
          <Card
            sx={style}
            title={'Warning'}
            action={
              <IconButton aria-label="close-icon" onClick={handleClose}>
                <CloseIcon
                  sx={{
                    justifyContent: 'center',
                  }}
                />
              </IconButton>
            }
          >
            <Container
              sx={{
                height: '155px',
                width: '100%',
                justifyContent: 'center',
              }}
              disableGutters={true}
            >
              <Container
                sx={{
                  display: 'flex',
                  paddingBottom: '16px',
                  justifyContent: 'center',
                }}
              >
                <WarningIcon />
              </Container>
              <Typography
                sx={{
                  display: 'flex',
                  fontSize: '18px',
                  paddingBottom: '6px',
                  justifyContent: 'center',
                }}
              >
                Warning
              </Typography>
              <Typography
                sx={{
                  display: 'flex',
                  fontSize: '15px',
                  paddingBottom: '32px',
                  textAlign: 'center',
                  justifyContent: 'center',
                }}
              >
                Exercise caution when editing your URL and Access Token. If you
                edit incorrectly, Smart Search may not be able to connect to
                your install.
              </Typography>
            </Container>
            <Container maxWidth="xl" disableGutters={true}>
              <Container
                sx={{
                  borderTop: '1px solid #EAECF0',
                  height: '64px',
                  width: '600px',
                  padding: '0px',
                  marginLeft: '-25px',
                  marginTop: '25px',
                  background: '#F9FAFB',
                  display: 'flex',
                  justifyContent: 'flex-end',
                  gap: '8px',
                  alignItems: 'center',
                }}
              >
                <ThemeProvider theme={theme}>
                  <Button
                    sx={{ alignContent: 'right' }}
                    variant={ButtonVariant.Primary}
                    outline
                    onClick={handleClose}
                    data-testid="modal-cancel-button"
                  >
                    Cancel
                  </Button>
                  <Button
                    sx={{ alignContent: 'right' }}
                    variant={ButtonVariant.Primary}
                    onClick={handleConfirmClose}
                    data-testid="modal-save-button"
                  >
                    Continue
                  </Button>
                </ThemeProvider>
              </Container>
            </Container>
          </Card>
        </Box>
      </Modal>

      <Card
        title="Settings"
        sx={{
          '&.MuiCardContent-root': { paddingBottom: '32px' },
        }}
      >
        <Banner severity="warning" sx={{ fontSize: '13px' }} title="Warning">
          Your URL and access token are the credentials this install uses to
          access and communicate with Smart Search. Exercise caution when
          editing your URL and Access Token
        </Banner>
        <Form onSubmit={handleSubmit} style={{ width: '100%' }}>
          <Form.LabeledInputField label="URL">
            <Form.CopyableInput
              testId="settings-url"
              value={url}
              type="url"
              onChange={handleUrlChange}
            />
          </Form.LabeledInputField>
          <Form.LabeledInputField label="Access Token">
            <Form.CopyableInput
              testId="settings-token"
              value={token}
              type="text"
              isSensitive
              onChange={handleTokenChange}
            />
          </Form.LabeledInputField>
          <div style={{ marginTop: '24px' }}>
            <EmotionThemeProvider theme={theme}>
              {errorMessage && (
                <>
                  <Banner dataTestid="settings-message" severity={'error'}>
                    {errorMessage}
                  </Banner>
                  <br />
                </>
              )}
              <Button
                size={ButtonSize.Medium}
                variant={ButtonVariant.Primary}
                ref={submitButton}
                data-testid="save-configuration-button"
                disabled={!configChanged}
              >
                Save Configuration
              </Button>
            </EmotionThemeProvider>
          </div>
        </Form>
      </Card>
    </ThemeProvider>
  );
}

export default Settings;
