import { useState, useEffect, useRef, useCallback } from 'react';
import { isAxiosError } from 'axios';
import {
  SyncData as SyncResponse,
  SyncStatus,
} from '../pages/SyncData/types.d';
import axios from '../utils/axios';

// Define the hook
const useSyncData = () => {
  const [deleteStatus, setDeleteStatus] = useState(SyncStatus.Init);
  const [deleteMessage, setDeleteMessage] = useState('');
  const [syncData, setSyncData] = useState<SyncResponse | null>(null);
  const [syncStatus, setSyncStatus] = useState(SyncStatus.Init);
  const [syncMessage, setSyncMessage] = useState('');
  const [progress, setProgress] = useState(0);
  const pollingRef = useRef<boolean>(true);
  const [syncUuid, setSyncUuid] = useState<string>('');
  const abortControllerRef = useRef<AbortController | null>(null);

  const runSyncData = useCallback(
    async (initialUuid?: string) => {
      let uuid: string = initialUuid ?? syncUuid ?? '';

      let currentState: SyncStatus;

      setProgress(0);
      setSyncMessage('');
      setDeleteMessage('');
      setSyncStatus(SyncStatus.Pending);
      abortControllerRef.current = new AbortController();

      try {
        do {
          if (!pollingRef.current) break; // Check if polling is cancelled

          const { data } = await axios.put<SyncResponse>(
            '/sync-data',
            {
              uuid,
              siteId: window.smartSearchSettings.siteId,
            },
            {
              signal: abortControllerRef.current.signal,
            }
          );
          uuid = data.uuid || uuid;
          currentState = data.status;
          setSyncStatus(data.status);
          setSyncMessage(data.message);
          setProgress(data.progress);
          setSyncData(data);
          setSyncUuid(uuid);
        } while (currentState === SyncStatus.Pending);
      } catch (err) {
        if (isAxiosError(err)) {
          if (err.name === 'CanceledError') {
            console.log('Sync cancelled');
            return;
          }
        }
        setSyncStatus(SyncStatus.Error);
        setSyncMessage(
          'Unexpected sync error occurred. Please "Resume" the sync. If this fails "Delete" and begin sync process again.'
        );
      }
    },
    [syncUuid]
  );

  const deleteSyncData = useCallback(async () => {
    setSyncMessage('');
    setDeleteMessage('');

    try {
      setDeleteStatus(SyncStatus.Pending);
      const { data } = await axios.delete('/sync-data');

      setDeleteStatus(data.status);
      setDeleteMessage(data.message);
    } catch (err) {
      setDeleteStatus(SyncStatus.Error);
      setDeleteMessage('Unexpected error occurred. Please contact support.');
    }
  }, []);

  const cancelSync = useCallback(() => {
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
    pollingRef.current = false;
    setSyncStatus(SyncStatus.Init);
    setSyncMessage('Sync cancelled.');
    setSyncData(null);
    setSyncUuid('');
  }, []);

  useEffect(() => {
    // Reset the polling flag when the component mounts
    pollingRef.current = true;

    // Cleanup function to handle component unmounting
    return () => {
      pollingRef.current = false;
    };
  }, []);

  const startPolling = useCallback(() => {
    pollingRef.current = true;
    runSyncData(syncUuid);
  }, [runSyncData, syncUuid]);

  return {
    startPolling,
    syncData,
    syncStatus,
    syncMessage,
    deleteStatus,
    deleteMessage,
    progress,
    useCallback,
    cancelSync,
    deleteSyncData,
  };
};

export default useSyncData;
