import { Box, FormControlLabel, Radio, Typography } from '@mui/material';
import styled from '@emotion/styled';

const radioButtonStyles = (isChecked: boolean) =>
  ({
    padding: '32px',
    border: `${isChecked ? '2px' : '1px'} solid ${
      isChecked ? '#0783be' : '#eaecf0'
    }`,
    borderRadius: '16px',
    position: 'relative',
    minWidth: '330px',
  }) as const;
const RadioButtonContainer = styled.fieldset`
  &:disabled {
    cursor: not-allowed;
    opacity: 0.7;
  }
`;

const RadioLabel = styled(FormControlLabel)`
  && .MuiFormControlLabel-label {
    color: '#4285f4';
    display: none;
  }
  position: absolute;
  top: 4px;
  right: 16px;
`;
interface LabeledRadioFieldProps {
  label: React.ReactNode;
  value: string;
  isChecked: boolean;
  isDisabled?: boolean;
  onChange?: (value: string) => void;
}
const LabeledRadioField = ({
  label,
  value,
  onChange,
  isChecked,
  isDisabled,
  children,
}: React.PropsWithChildren<LabeledRadioFieldProps>) => {
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (onChange) onChange(event.target.value);
  };

  const handleOnClick = (event: React.MouseEvent<HTMLFieldSetElement>) => {
    event.preventDefault();
    if (isDisabled) return;
    if (onChange) onChange(value);
  };

  return (
    <RadioButtonContainer
      style={radioButtonStyles(isChecked)}
      onMouseDown={handleOnClick}
      disabled={isDisabled}
      name={value}
    >
      <RadioLabel
        value={value}
        control={
          <Radio
            sx={{ color: '#b2bcc7' }}
            checked={isChecked}
            onChange={handleChange}
          />
        }
        label={value}
        labelPlacement="start"
      />
      <Typography component="div" sx={{ marginBottom: 1 }}>
        {label}
      </Typography>
      <Box sx={{ fontWeight: '400', color: '#667085', width: '272px' }}>
        {children}
      </Box>
    </RadioButtonContainer>
  );
};

export default LabeledRadioField;
