import React from 'react';
import { darken, lighten } from 'polished';
import styled from '@emotion/styled';
import { SxProps } from '@mui/material';

export enum ButtonVariant {
  Primary = 'primary',
  Danger = 'danger',
}

export enum ButtonSize {
  Large = 'large',
  Medium = 'medium',
  Small = 'small',
}

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  variant?: ButtonVariant;
  outline?: boolean;
  block?: boolean;
  size?: ButtonSize;
  sx?: SxProps;
}

export const Button = styled.button<ButtonProps>`
  ${({ block = false }) =>
    block
      ? {
          display: 'block',
          width: '100%',
        }
      : {
          display: 'inline-flex',
        }}

  line-height: 21px;
  padding: 8px 12px 8px 12px;
  border-width: thin;
  border-radius: 6px;
  border-style: solid;
  cursor: pointer;
  font-weight: 400;
  transition: color 0.15s;
  font-size: 1rem;
  height: 40px;
  ${({
    sx,
    theme,
    outline,
    variant = ButtonVariant.Primary,
    size = ButtonSize.Medium,
  }) => ({
    fontSize: `${theme.fonts.sizes[size]} !important`,
    backgroundColor: outline ? 'transparent' : theme.colors[variant],
    borderColor: outline ? theme.colors[variant] : 'transparent',
    color: outline ? theme.colors[variant] : theme.colors.white,
  })};

  &:hover,
  &:focus {
    ${({ theme, outline, variant = ButtonVariant.Primary }) => ({
      backgroundColor: outline
        ? lighten(0.5, theme.colors[variant])
        : darken(0.1, theme.colors[variant]),
    })};
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  &:focus {
    outline: 1px solid transparent;
  }
`;
