import React from 'react';
import styled from '@emotion/styled';
import { space, compose, SpaceProps } from 'styled-system';

const StyledIcon = styled('span')(compose(space));

interface IconProps extends SpaceProps {
  name: string;
  style?: React.CSSProperties;
}

function Icon({ name, style, ...rest }: IconProps) {
  return (
    <StyledIcon
      className={`dashicons dashicons-${name}`}
      style={style}
      {...rest}
    />
  );
}

export default Icon;
