import React from 'react';
import { Switch } from '@mui/material';
import { styled } from '@mui/material/styles';

interface StyledSwitchProps {
  checked: boolean;
  inputProps: { role: string };
  onChange: (
    _event: React.ChangeEvent<HTMLInputElement>,
    newValue: boolean
  ) => void;
}

const StyledSwitchBase = styled((props: StyledSwitchProps) => (
  <Switch disableRipple {...props} />
))(({ theme }) => ({
  width: 36,
  height: 20,
  padding: 0,
  margin: '10px',
  '& .MuiSwitch-switchBase': {
    padding: 0,
    margin: 2,
    '&.Mui-checked': {
      transform: 'translateX(16px)',
      '& + .MuiSwitch-track': {
        opacity: 1,
        border: 0,
      },
    },
  },
  '& .MuiSwitch-thumb': {
    background: '#FFFFFF',
    boxShadow:
      '0px 1px 3px rgba(16, 24, 40, 0.1), 0px 1px 2px rgba(16, 24, 40, 0.06)',
    width: 16,
    height: 16,
    transition: theme.transitions.create(['width'], {
      duration: 200,
    }),
  },
  '& .MuiSwitch-track': {
    borderRadius: '12px',
    backgroundColor: '#C6CBD3',
    transition: theme.transitions.create(['background-color'], {
      duration: 500,
    }),
  },
}));

const StyledSwitch: React.FC<StyledSwitchProps> = ({
  checked,
  inputProps,
  onChange,
}) => {
  return (
    <StyledSwitchBase
      checked={checked}
      inputProps={inputProps}
      onChange={(_event: React.ChangeEvent<HTMLInputElement>, newValue) =>
        onChange(_event, newValue)
      }
    />
  );
};

export default StyledSwitch;
