export const HybridSettingsCard = ({
  sx,
  searchBias,
  setSearchBias,
  action,
  disabled,
}: {
  sx?: SxProps;
  searchBias: number;
  setSearchBias: (value: number) => void;
  action?: React.ReactNode;
  disabled?: boolean;
}) => {
  return (
    <Card sx={sx} title="Hybrid settings" action={action}>
      <Grid container>
        <Grid item xs={8}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography
              variant="h6"
              gutterBottom
              sx={{
                color: '#475467',
                fontSize: '16px',
                marginRight: '8px',
                marginBottom: '0px',
              }}
            >
              Hybrid search weighting
            </Typography>
            <Chip
              sx={{
                border: '1px solid #CFDDE9',
                padding: '6px, 12px, 6px, 12px',
                backgroundColor: '#F4F7FA',
                color: '#3e8bff',
                height: 'auto',
              }}
              label={
                <Box display="flex" alignItems="center">
                  AI Powered <SparkleIcon />
                </Box>
              }
            />
          </Box>
          <Typography
            variant="subtitle1"
            gutterBottom
            sx={{ color: '#667085', fontSize: '15px' }}
          >
            Drag the slider below to weight your search between Full Text and
            Semantic, we recommend Hybrid as a good staring point.
          </Typography>
        </Grid>
      </Grid>
      <Grid container spacing={2} mt={1}>
        <Grid item xs={8}>
          <Slider
            data-testid="hybrid-slider"
            disabled={disabled}
            sx={{
              width: '130%',
              height: '8px',
            }}
            track={false}
            aria-label="config"
            min={1}
            max={9}
            marks={marks}
            value={searchBias}
            onChange={(_event, newValue) => setSearchBias(newValue as number)}
          />
        </Grid>
      </Grid>
    </Card>
  );
};

export const SemanticSettingsCard = ({
  sx,
  action,
  fields,
  removeField,
  handleOpen,
  disabled,
}: {
  sx?: SxProps;
  action?: React.ReactNode;
  fields: string[];
  removeField: () => void;
  handleOpen: () => void;
  disabled?: boolean;
}) => {
  return (
    <Card title="Semantic settings" action={action} sx={sx}>
      <Grid container justifyContent="space-between">
        <Grid item xs={10}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography
              variant="h6"
              gutterBottom
              sx={{
                color: '#475467',
                fontSize: '16px',
                marginRight: '8px',
                marginBottom: '0px',
              }}
            >
              Fields
            </Typography>
          </Box>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <Typography
              variant="subtitle1"
              gutterBottom
              sx={{ color: '#667085', fontSize: '15px' }}
            >
              Select a field/s from your database to leverage for semantic
              search. The field(s) you select will run through our Machine
              Learning algorithm and made available for Semantic or Hybrid
              search. Click ‘Save and configure’ to confirm your selection.
            </Typography>
          </Box>
        </Grid>
        <Grid
          item
          xs={2}
          sx={{
            display: 'flex',
            justifyContent: 'right',
            margin: 'auto',
          }}
        >
          <ThemeProvider theme={theme}>
            <Button
              sx={{ alignContent: 'right' }}
              variant={ButtonVariant.Primary}
              onClick={handleOpen}
              data-testid="modal-button"
              disabled={disabled}
            >
              {fields.length > 0 ? 'Edit Field' : 'Add Field'}
            </Button>
          </ThemeProvider>
        </Grid>
      </Grid>
      <Divider variant="fullWidth" sx={{ width: '100%', color: '#EAECF0' }} />
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <Grid container mt={1} width={'125px'}>
          <Typography
            variant="h6"
            gutterBottom
            sx={{
              width: '115px',
              color: '#475467',
              fontSize: '16px',
              marginRight: '8px',
              marginBottom: '0px',
            }}
          >
            Selected Fields:
          </Typography>
        </Grid>
        <Grid container mt={1} data-testid="semantic-chip">
          {fields.map((field) => (
            <Chip
              sx={{
                backgroundColor: '#DBEAFE',
                borderColor: 'primary',
              }}
              disabled={disabled}
              key={`field-${field}`}
              color="primary"
              label={field}
              size="medium"
              variant="outlined"
              onDelete={() => removeField()}
            />
          ))}
        </Grid>
      </Box>
    </Card>
  );
};

export const AddSemanticFieldsCard = ({
  config,
  fields,
  chooseField,
  textValue,
  handleTextValueChange,
  handleClose,
}: {
  config: Configs | null;
  fields: string[];
  chooseField: (field: string) => void;
  handleClose: () => void;
  textValue: string;
  handleTextValueChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
}) => {
  return (
    <Card
      sx={modalStyle}
      title={'Add Field'}
      action={
        <IconButton aria-label="close-icon" onClick={handleClose}>
          <CloseIcon
            sx={{
              justifyContent: 'center',
            }}
          />
        </IconButton>
      }
    >
      <TextField
        value={textValue}
        onChange={handleTextValueChange}
        sx={{
          display: 'flex',
          alignItems: 'flex-start',
          paddingLeft: '12px',
          width: '420px',
          height: '40px',
          background: '#FFFFFF',
          border: '1px solid #CCD0D6',
          borderRadius: '8px',

          justifyContent: 'center',
          '& .MuiInputBase-input': {
            border: 'none',
            ':focus': {
              boxShadow: 'none',
            },
          },
        }}
        placeholder="Search fields..."
        variant="standard"
        InputProps={{
          startAdornment: <SearchIcon />,
          disableUnderline: true,
        }}
      />
      <Container maxWidth="xl" disableGutters={true}>
        <Container
          maxWidth="xl"
          disableGutters={true}
          sx={{
            overflow: 'hidden',
            borderRadius: '8px',
            width: '432px',
            marginTop: '-15px',
            border: '1px solid #EAECF0',
          }}
        >
          <Container
            maxWidth="xl"
            disableGutters={true}
            sx={{
              overflow: 'auto',
              height: '360px',
            }}
          >
            {!config && (
              <SearchConfigError error="No config found, please try reloading the page." />
            )}
            {config &&
              Object.keys(config.models).map((modelName) => {
                const filteredFields = Object.entries(config.models[modelName])
                  .filter(
                    ([key, value]) =>
                      !value.has_sub_fields && key.includes(textValue)
                  )
                  .map(([field]) => field);

                if (filteredFields.length === 0) {
                  return null;
                }

                return (
                  <List
                    key={`bordered-list-${modelName}`}
                    sx={{
                      paddingTop: '0px',
                    }}
                  >
                    <AppBar position="sticky" elevation={0}>
                      <Typography
                        sx={{
                          backgroundColor: '#F9FAFB',
                          height: '28px',
                          paddingLeft: '8px',
                          fontSize: '10px',
                          color: '#6B7280',
                          alignContent: 'center',
                        }}
                      >
                        {modelName[0].toUpperCase() + modelName.slice(1)}
                      </Typography>
                    </AppBar>
                    {filteredFields.map((field) => (
                      <ListItem
                        key={`list-item-${modelName}-${field}`}
                        secondaryAction={
                          fields.includes(field) ? (
                            <DoneIcon
                              sx={{
                                width: '13.5px',
                                height: '9.76px',
                                color: '#0783BE',
                              }}
                            />
                          ) : null
                        }
                        disablePadding
                        sx={{
                          marginBottom: '0px',
                        }}
                      >
                        <ListItemButton
                          key={`list-item-${modelName}-${field}-button`}
                          onClick={() => chooseField(field)}
                          sx={{
                            borderTop: '1px solid #EAECF0',
                            paddingLeft: '5px',
                            height: '40px',
                            alignContent: 'center',
                            fontSize: '13px',
                            ':hover': {
                              backgroundColor: '#0783BE0F',
                            },
                          }}
                          selected={fields.includes(field)}
                        >
                          <ListItemText>{field}</ListItemText>
                        </ListItemButton>
                      </ListItem>
                    ))}
                  </List>
                );
              })}
          </Container>
        </Container>

        {/* Gradient */}
        <Container
          sx={{
            marginTop: '-20px',
            height: '20px',
            padding: '0px !important',
            width: '430px',
            background:
              'linear-gradient(0deg, rgba(255,255,255,1), rgba(255,0,0,0))',
            opacity: 0.7,
          }}
        ></Container>
        <Container
          sx={{
            borderTop: '1px solid #EAECF0',
            height: '64px',
            width: '480px',
            padding: '0px',
            marginLeft: '-25px',
            marginTop: '25px',
            background: '#F9FAFB',
            display: 'flex',
            justifyContent: 'flex-end',
            gap: '8px',
            alignItems: 'center',
          }}
        >
          <ThemeProvider theme={theme}>
            <Button
              sx={{ alignContent: 'right' }}
              variant={ButtonVariant.Primary}
              outline
              onClick={handleClose}
              data-testid="modal-cancel-button"
            >
              Cancel
            </Button>
            <Button
              sx={{ alignContent: 'right' }}
              variant={ButtonVariant.Primary}
              onClick={handleClose}
              data-testid="modal-add-button"
            >
              Add Field
            </Button>
          </ThemeProvider>
        </Container>
      </Container>
    </Card>
  );
};
