import { useState, useEffect, useCallback } from 'react';
import { AxiosError } from 'axios';
import api from '../utils/axios';
import { Configs } from '../pages/Config/types';

const useFetchSearchConfig = () => {
  const [config, setConfig] = useState<Configs | null>(null);
  const [modelCount, setModelCount] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [errorTitle, setErrorTitle] = useState<string | null>(null);

  useEffect(() => {
    const controller = new AbortController();

    const fetchSettingsData = async () => {
      try {
        setLoading(true);
        const { data } = await api.get<Configs>('search-config', {
          signal: controller.signal,
        });
        data.searchType = data.searchType || 'fullText';
        setConfig(data);
        setModelCount(Object.keys(data.models).length);
      } catch (e) {
        if (e instanceof AxiosError) {
          setError(e.response?.data.message);
        } else {
          setErrorTitle('Something went wrong');
          setError('please reload the page again.');
        }
      } finally {
        setLoading(false);
      }
    };

    fetchSettingsData();

    return () => {
      controller.abort();
    };
  }, []);

  const saveSearchConfig = useCallback(async (config: Configs | null) => {
    setLoading(true);
    try {
      await api.post('search-config', config);
      return true;
    } catch (e) {
      if (e instanceof AxiosError) {
        setError(e.response?.data.message);
      } else {
        setErrorTitle('Something went wrong');
        setError('please reload the page again.');
      }
      return false;
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    config,
    modelCount,
    loading,
    error,
    errorTitle,
    setConfig,
    saveSearchConfig,
  };
};

export default useFetchSearchConfig;
