export enum SyncStatus {
  Init = 'init',
  Pending = 'pending',
  Completed = 'completed',
  Error = 'error',
}

export type SyncData = {
  uuid: string;
  siteId: string;
  siteName: string;
  progress: number;
  status: SyncStatus;
  currentSyncedType: string;
  allSyncedTypes: string[];
  message: string;
};
