import styled from '@emotion/styled';

import { Box, Text } from 'rebass';

export enum ProgressBarStyle {
  progressWorking = 'progressWorking',
}

const Container = styled(Box)`
  padding-left: 0px;
  height: 8px;
  width: 100%;
  border-radius: 100px;
`;

type ProgressProps = {
  value: number;
  status: ProgressStatusColor;
};

export enum ProgressStatusColor {
  Primary = 'primary',
  Danger = 'danger',
  Complete = 'complete',
}

const progressBarStyleMap: Record<
  ProgressStatusColor,
  'progressWorking' | 'progressSuccess' | 'progressError'
> = {
  [ProgressStatusColor.Primary]: 'progressWorking',
  [ProgressStatusColor.Complete]: 'progressSuccess',
  [ProgressStatusColor.Danger]: 'progressError',
};

const Progress = styled.div<ProgressProps>`
  height: 8px;
  border-radius: 100px;
  width: ${(props) => props.value}%;
  background-color: ${(props) =>
    props.theme.colors[progressBarStyleMap[props.status]]};
  transition: width 1s ease-in-out;
`;

type ProgressBarProps = {
  value: number;
  status: ProgressStatusColor;
};

const ProgressBar = ({ value = 0, status, ...rest }: ProgressBarProps) => {
  return (
    <Container backgroundColor="whitesmoke" {...rest}>
      <Progress value={value} status={status}>
        <Text
          fontWeight={600}
          fontSize={20}
          textAlign="center"
          p={15}
          color="#fff"
        >
          {Math.round((value / 100) * 100)}%
        </Text>
      </Progress>
    </Container>
  );
};

export default ProgressBar;
