import React, { useState } from 'react';
import { IconButton, useTheme } from '@mui/material';
import { ReactComponent as ContentCopyIcon } from './content-copy.svg';
import styled from '@emotion/styled';
import { Input as InputField } from './Input';
import Icon from '../Icon';

const sensitiveButtonWidth = 40;

const StyledInputContainer = styled.div`
  position: relative;
  width: 100%;
  display: inline-flex;
`;

const StyledInput = styled(InputField)`
  user-select: text;
  padding-right: 30px !important;
  width: 100%;
  min-width: 200px;
`;

interface CopyableTextFieldProps {
  value: string;
  onChange: (e: React.ChangeEvent<HTMLInputElement>) => void;
  type: string;
  isSensitive?: boolean;
  testId?: string;
}

const CopyableInputField = ({
  value,
  onChange,
  type,
  isSensitive = false,
  testId,
}: CopyableTextFieldProps) => {
  const theme = useTheme();
  const [isCopied, setIsCopied] = useState(false);
  const [isVisible, setIsVisible] = useState(!isSensitive);

  const toggleVisibility = () => setIsVisible(!isVisible);
  const handleCopy = () => {
    navigator.clipboard.writeText(value);
    setIsCopied(true);
    setTimeout(() => setIsCopied(false), 1000);
  };

  return (
    <StyledInputContainer>
      <StyledInput
        value={value}
        onChange={onChange}
        type={isVisible ? type : 'password'}
        data-testid={testId}
        autoComplete="new-password"
        style={{
          borderRadius: isSensitive ? '4px 0 0 4px' : '4px', // Conditional border-radius
        }}
      />
      <IconButton
        onClick={handleCopy}
        style={{
          position: 'absolute',
          right: 10 + (isSensitive ? sensitiveButtonWidth : 0),
          padding: 0,
          top: 3,
          fontSize: 10,
        }}
      >
        {isCopied ? (
          'Copied!'
        ) : (
          <ContentCopyIcon color={theme.palette.primary.main} />
        )}
      </IconButton>
      {isSensitive && (
        <IconButton
          onClick={toggleVisibility}
          sx={{
            width: sensitiveButtonWidth,
            borderRadius: '0 4px 4px 0!important',
            background: '#F9FAFB',
            border: '1px solid #C6CBD3',
            marginLeft: '-2px',
            boxShadow: '0px 1px 2px rgba(16, 24, 40, 0.1) !important;',
          }}
        >
          <Icon name="visibility" />
        </IconButton>
      )}
    </StyledInputContainer>
  );
};

export default CopyableInputField;
