import * as React from 'react';
import styled from '@emotion/styled';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';

import { ReactComponent as ExpandMoreIcon } from './expand-more.svg';

interface CollapsibleProps {
  title: React.ReactNode;
  ExpandIcon?: React.ReactNode;
  CollapseIcon?: React.ReactNode;
  expanded?: boolean;
}

const StyledAccordionContainer = styled.div`
  margin-bottom: 24px;
  background: #ffffff;
  border-radius: 100px;
  box-shadow: none;
`;

const StyledAccordionHeader = styled(AccordionSummary, {
  shouldForwardProp: (prop) => prop !== 'fullWidth',
})`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
  gap: 8px;
  height: 50px;
  background: #f9fafb;

  &.MuiAccordionSummary-root {
    border-radius: 8px;
  }

  &.Mui-expanded {
    opacity: 1;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
  }
`;

const StyledAccordion = styled(Accordion)`
  gap: 0px;
  border: 1px solid rgba(0, 0, 0, 0.08);
  justify-content: space-between;
  background-color: #fff;

  &.MuiAccordion-root {
    box-shadow: none;
    border-radius: 8px;
  }
`;

const StyledAccordionDetails = styled(AccordionDetails)`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;

  & > *:first-of-type {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    max-width: 100%;
  }
`;

function Collapsible({
  expanded,
  children,
  title,
  ExpandIcon,
  CollapseIcon,
  ...props
}: React.PropsWithChildren<CollapsibleProps>) {
  return (
    <StyledAccordionContainer>
      <StyledAccordion {...props} disableGutters expanded={expanded}>
        <StyledAccordionHeader expandIcon={<ExpandMoreIcon />}>
          {title}
        </StyledAccordionHeader>
        <StyledAccordionDetails>{children}</StyledAccordionDetails>
      </StyledAccordion>
    </StyledAccordionContainer>
  );
}

export default Collapsible;
